.Menu{
  margin: 15%;
  margin-top: 5%;
  padding: 15px;
  padding-bottom: 5px;
  text-align: center;
  color: #5b5;
  background-color: #191919;
  margin-bottom: 5px;
  border: 2px solid #333;
  border-radius: 30px;
}

.Menu h1{
  margin-left:10vh;
  margin-right:10vh;
  margin-bottom:10px;
}

.Menu p{
  margin-top: 10px;
  font-size: 20px;
  color: #383;
}

.Menu .Buttons{
  display: grid;
}

@media(min-width:750px){
  .Menu h1{
    margin-left:40%;
    margin-right:40%;
  }
  .Menu .Buttons{
    gap: 10px;
    grid-template-columns: 1fr 1fr;
  }
}

.Menu button{
  cursor: pointer;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 150%;
  border-radius:20px;
  box-shadow: 1px 1px 2px #666;
  border:1px solid #aaa;
  background: #222;
  width: 100%;
  color: #5b5;
}

.Menu button:hover{
  background: #333;
  color: #4a4;
}

.Menu input{
  width: 100%;
  height: 50px;
  font-size: 25px;
  border-radius: 30px;
  padding-left: 10px;
  border:1px solid #aaa;
  color: #51bb57;
  background-color: #222;
  margin-bottom: 15px;
}

.Menu input:hover{
  background-color: #333;
}
